@include body-class(true) {

  .dashui-login-form {
    width: 100%;
    max-width: 500px;
	  padding: $sui-gutter;
    margin: 0 auto;
    text-align: center;
	  @extend .sui-box;

	  @include media(min-width, md) {
		  padding: 60px;
	  }

	  h2 {
      margin: 0 0 $sui-gutter-md;
      line-height: 30px;
    }

	  .sui-side-tabs {
		  margin: 0;

		  > .sui-tabs-menu {
			  justify-content: center;

			  > .sui-tab-item {
				  flex: 1 0 0;
				  max-width: 156px;
			  }
		  }
	  }

	  .form-separator {
		  margin: 30px 0;

		  p {
			  display: flex;
			  flex-direction: row;
			  color: #888888;
			  font-size: 13px;
		  }

		  p:before,
		  p:after {
			  content: "";
			  flex: 1 1;
			  border-bottom: 1px solid #E6E6E6;
			  margin: auto;
		  }

		  p:before {
			  margin-right: 10px
		  }

		  p:after {
			  margin-left: 10px
		  }
	  }

	  .sui-form-field {
		  margin-bottom: $sui-gutter-md;
		  text-align: left;
	  }

	  .dashui-auth-form-footer {
		  display: flex;
		  align-items: center;
		  justify-content: space-between;
		  margin-top: $sui-gutter-md;

		  a {
			  color: $font-color;

			  &:hover {
				  color: $a-action-color;
			  }
		  }
	  }
  }
}